@import "_flexboxgrid.scss";

.header {
    background-color: #fff;
    min-height: 100px;
    width: 100%;

    &__container {
        @extend .col-lg-9;
        @extend .row;
        @extend .reverse;

        border-bottom: 0.5px solid purple !important;
        margin: 0 !important;

    }
}

.nav {
    @extend .col-xs-9;
    @extend .row;
    @extend .reverse;

    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -webkit-flex-direction: row-reverse;
    -moz-box-orient: horizontal;
    -moz-box-direction: reverse;
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
    height: 100px;
    max-width: 55%;

}

.nav__link {
    @extend .col-xs;
    font-family: Lato, sans-serif;
    font-size: 26px;
    color: #000;
    text-decoration: none;
    margin-left: 20px;
    position: relative;
    top: 36px;
    height: 73px;

    &:hover {
        color: #000 !important;
        text-decoration: underline;
    }

    &:visited {
        color: #d8d8d8;
    }
}

.nav__link--active {
    color: #000 !important;
    font-weight: 700;
    text-decoration: underline;
}