@import "_flexboxgrid.scss";

.imageCard {
    @extend .col-xs-3;

    -webkit-box-flex: 2;
    -webkit-flex-grow: 2;
    -moz-box-flex: 2;
    -ms-flex-positive: 2;
    flex-grow: 2;
    padding-left: 15px !important;

    &__image {
        border-radius: 100%;
        position: relative;
        top: -50px;
        width: 250px;
        height: 250px;
        border-radius: 50%;
        padding: 3px;
        border: 3px solid purple;
    }
}