@import "_flexboxgrid.scss";

.bio {
    @extend .col-lg-9;

    -webkit-box-flex: 8;
    -webkit-flex-grow: 8;
    -moz-box-flex: 8;
    -ms-flex-positive: 8;
    flex-grow: 8;
    padding-right: 15px;

    &__content,
    &__title,
    &__subheader {
        text-align: right;
    }

    &__title,
    &__subheader {
        font-family: "Lato", sans-serif;
        margin-top: 25px;
        margin-bottom: 36px;
    }
}