@import "_flexboxgrid.scss";

.footer {
    @extend .row;
    @extend .center-lg;

    border-top: 0.5px solid purple;
    margin-top: 20px !important;
    margin: 0 5rem;

    width: 100%;
    flex-shrink: 0;

    &__copyright {
        text-align: center;
        color: #000;
        font-size: 18px;
    }

    &__email {
        color: #8c8b8b;
    }

}